import * as React from 'react';
import hd from "./Header.module.css"
import logo from "./images/logo.png"
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faAngleDown,faSearch,faMoon,faUserTie} from '@fortawesome/free-solid-svg-icons'

const Header=(props:any)=>{

    return(<div className={hd.HeaderBody}>

        <div className={hd.navBar}>
            <img src={logo} className={hd.logo}></img>
            <div>
            <ul className={hd.navBarMain}>
                <li> <a data-content={"ТБ"} href={"#"} >ТБ</a></li>
                <li> <a  className={hd.test} data-content={"Фільми"} href={"#"}>Фільми</a></li>
                <li> <a data-content={"Прем"} href={"#"}>Прем'єри</a></li>
                <li> <a data-content={"Тарифи"} href={"#"}>Тарифи</a></li>
                <li> <a data-content={"Як підключити"} href={"#"}>Як підключити</a></li>
                <li> <a data-content={"Промокод"} href={"#"}>Промокод</a></li>
                <li className={hd.moreList}> <a data-content={"Ще"}  href={"#"}>Ще <FontAwesomeIcon icon={faAngleDown} /></a>
                <ul  className={hd.more}>
                    <ul  className={hd.test}>
                        <li><a>Оплата</a></li>
                        <li><a>Серіали</a></li>
                        <li><a>Мультфільми</a></li>

                    </ul>


                </ul>

                </li>
            </ul>
            </div>
        <div className={hd.navBarOther}>
            <a><FontAwesomeIcon icon={faSearch} size={"lg"} /></a>
            <a><FontAwesomeIcon icon={faMoon} size={"lg"} /></a>
            <a>UK<FontAwesomeIcon icon={faAngleDown} size={"lg"} /></a>
            <a><FontAwesomeIcon icon={faUserTie} size={"lg"} />Увійти</a>
        </div>
        </div>


</div>)

}

export default Header;